import React,{useState} from 'react';
import db from '../db.js'

import { Table } from 'antd';

 // { name: '',
  //   date: '2 5',
  //   dateNum: 34,
  //   class: '',
  //   grade: 'G3',
  //   place: '',
  //   ground: '',

  //   distance: '1400',
  //   distanceType: '',
  //   order: '' }
  // { class: [ '', '', '', '' ],
  // grade: [ 'OP', 'G3', 'Pre-OP', 'G2', 'G1' ],
  // place: [ '', '', '', '', '', '', '', '', '', '', '' ],
  // ground: [ '', '' ],
  // distanceType: [ '', '', '', '' ],
  // direction: [ '', '', '' ],
  // side: [ null, '', '', '' ] }
 const filterList= { class:
    [ { text: '/', value: '' },
      { text: '/', value: '' },
      { text: '&/', value: '' },
      { text: '/', value: '' } ],
   grade:
   [
      { text: 'Pre-OP', value: 'Pre-OP' },
      { text: 'OP', value: 'OP' },
      { text: 'G1', value: 'G1' },
      { text: 'G2', value: 'G2' },
      { text: 'G3', value: 'G3' }
    ],
   place:
    [ { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ],
   ground: [ { text: '', value: '' }, { text: '', value: '' } ],
   distanceType:
   [
     { text: '', value: '' },
     { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ],
   direction:
    [ { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ],
   side:
    [ { text: '', value: null },
      { text: '', value: '' },
      { text: '', value: '' },
      { text: '', value: '' } ] }
  const labels = ["name", "date", "class", "grade",
                  "place", "ground", "distance",
                  "distanceType", "direction", "side"]
  const mediumLabels = ["name", "date", "class","grade","ground","distanceType"]
  const getColumns = (labels)=>{
    return labels.map(label=> {
      if(filterList['class']){
        return {
          title:label,
          dataIndex:label,
          filters:filterList[label],
          onFilter: (value, record) => record[label] === value,
        }
      }else{
        return{
          title:label,
          dataIndex:label
        }
      }
    })
  }

  const Race = (props) =>{
    const allRaceList = db.get('races').value().map((race,index)=>{
                      race.key=index
                      return race
                    })
    const [selectedRowKeys,setSelectedRowKeys] = useState([])
    let columns = getColumns(labels)
    if(props.type==='medium'){
      columns = getColumns(mediumLabels)
    }
    const onSelectChange = (selectedRowKeys,selectedRows)=>{
      props.onSelect(selectedRows)
      setSelectedRowKeys(selectedRowKeys)
    }
    const rowSelection = {
      selectedRowKeys,
      onChange:onSelectChange
    }
    // 
    const onChange = (pagination, filters, sorter, extra) => {
      props.onSelect([])
      setSelectedRowKeys([])
    }
    return(
      <Table rowSelection={props.onSelect?rowSelection:null} columns={columns}
      dataSource={allRaceList} onChange={onChange} pagination={false}/>
      )
  }

export  default Race
